import * as React from "react";
import { Field, Form, Formik, useFormikContext } from "formik";
import {
  Box,
  Button,
  CircularProgress,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Grid,
} from "@material-ui/core";
import { TextField } from "formik-material-ui";
import * as Yup from "yup";
import { DateTimePicker } from "formik-material-ui-pickers";
import DateFnsUtils from "@date-io/date-fns";
import { MuiPickersUtilsProvider } from "@material-ui/pickers";
import AppButton from "../atoms/AppButton";
import {
  BookingFormValue,
  BookingInfo,
  BookingResponse,
  bookTable,
} from "../../service/BookingService";
import { AxiosResponse } from "axios";
import Constant from "../../constant/Constant";
import {useHistory} from "react-router-dom";

const phoneRegExp = new RegExp(Constant.PHONE_REGEX);

const FormSchema = Yup.object().shape({
  name: Yup.string()
    .min(2, "Vui lng nhp trn 2 k t!")
    .max(50, "Vui lng nhp di 50 k t!")
    .required("Vui lng nhp tn ngi t bn"),
  phone: Yup.string().matches(phoneRegExp, "S in thai khng hp l"),
  bookingTime: Yup.date().required("Vui lng chn thi gian t bn"),
});

const FormContent = () => {
  const { submitForm, isSubmitting } = useFormikContext();
  return (
    <Form>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Field
            component={TextField}
            required
            type="text"
            name="name"
            label="H tn"
            variant="outlined"
            fullWidth
          />
        </Grid>
        <Grid item xs={6}>
          <Field
            component={TextField}
            required
            type="text"
            name="phone"
            label="S in thoi"
            variant="outlined"
            fullWidth
          />
        </Grid>
        <Grid item xs={6} />
        <Grid item xs={6}>
          <Field
            component={DateTimePicker}
            name="bookingTime"
            label="t bn lc"
            inputVariant="outlined"
            variant="inline"
            fullWidth
          />
        </Grid>
        <Grid item xs={6}>
          <Field
            component={TextField}
            type="number"
            name="numberOfPeople"
            label="S ngi"
            variant="outlined"
            fullWidth
          />
        </Grid>
        <Grid item xs={12}>
          <Field
            component={TextField}
            type="text"
            name="note"
            label="Ghi ch thm"
            variant="outlined"
            fullWidth
          />
        </Grid>
      </Grid>
      <Box mt={4}>
        <AppButton
          variant="contained"
          color="primary"
          disabled={isSubmitting}
          onClick={submitForm}
          disableElevation
        >
          T BN NGAY
        </AppButton>
      </Box>
      <Box textAlign="left" fontSize={12} fontStyle="italic">
        Cc thng tin * l bt buc
      </Box>{" "}
      <Box my={2}>{isSubmitting && <CircularProgress />}</Box>
    </Form>
  );
};

const BookingForm = () => {
  const [showDialog, setShowDialog] = React.useState(false);
  const history = useHistory();
  const handleOpenDialog = () => {
    setShowDialog(true);
  };

  const handleCloseDialog = () => {
    setShowDialog(false);
  };
  return (
    <MuiPickersUtilsProvider utils={DateFnsUtils}>
      <Formik
        initialValues={{
          name: "",
          phone: "",
          bookingTime: new Date(),
        }}
        validationSchema={FormSchema}
        onSubmit={async (booking: BookingFormValue, { setSubmitting }) => {
          setSubmitting(true);
          console.log(booking);
          const req: BookingInfo = {
            phoneNumber: booking.phone,
            customerName: booking.name,
            bookingTime: booking.bookingTime.getTime(),
            numberOfPeople: booking.numberOfPeople,
            note: booking.note,
          };
          try {
            const res: AxiosResponse<BookingResponse> = await bookTable(req);
            setSubmitting(false);
            if (res.data.code === 1) {
              history.push(`/booking-result/${res.data.bookingId}`);
              // handleOpenDialog();
            }else {
              alert(
                "t bn khng thnh cng, vui lng th li sau. Hoc gi in  t bn (+84) 377 46 03 04"
              );
            }
          } catch (e) {
            console.log("error booking", e);
            alert(
              "C li xy ra, vui lng th li sau. Hoc gi in  t bn (+84) 377 46 03 04"
            );
          }
        }}
      >
        <FormContent />
      </Formik>
      <Dialog open={showDialog} onClose={handleCloseDialog}>
        <DialogTitle>t bn thnh cng</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Thng tin t bn  c ghi nhn. Xin cm n qu khch
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog} color="primary">
            OK
          </Button>
        </DialogActions>
      </Dialog>
    </MuiPickersUtilsProvider>
  );
};

export default BookingForm;
